<template>
    <div class="confirmOrder_container">
        <section v-if="!showLoading">
            <head-top head-title="确认订单" goBack="true" signin-up="confirmOrder"></head-top>
            <router-link :to='{path:"/confirmOrder/chooseAddress",query:{id:checkoutData.cart.id,sig:checkoutData.sig}}' class="address_container">
                <div class="address_empty_left">
                    <svg class="location_icon">
                        <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#location"></use>
                    </svg>
                    <div class="add_address" v-if="!choosedAddress">请添加收货地址{{choosedAddress}}</div>
                    <div v-else class="address_detail_container">
                        <header>
                            <span>{{choosedAddress.name}}</span>
                            <span>{{choosedAddress.sex=='1' ? '先生' : '女士'}}</span>
                            <span>{{choosedAddress.phone}}</span>
                        </header>
                        <div class="address-detail">
                            <span v-if="choosedAddress.tag" :style="{bakcgroundColor:iconColor(choosedAddress.tag)}">{{choosedAddress.tag}}</span>
                            <p>{{choosedAddress.address_detail}}</p>
                        </div>
                    </div>
                </div>
                <svg class="address_empty_right">
                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                </svg>
            </router-link>
            <section class="confirmOrder_songda">
                <p class="configOrder_txt">送达时间</p>
                <section class="configOrder_txs">
                    <p>预计送达 | 时间 {{checkoutData.delivery_reach_time}}</p>
                    <p v-if="checkoutData.cart.is_deliver_by_fengniao">蜂鸟专送</p>
                </section>
            </section>
            <section class="configOrder_zifu">
                <header class="header_style">
                    <span>支付方式</span>
                    <div class="more_type" @click="showPayWayFun">
                        <span>在线支付</span>
                        <svg class="address_empty_right">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                        </svg>
                    </div>
                </header>
                <section class="hongbo">
                    <span>红包</span>
                    <span>暂时只在饿了么 APP 中支持</span>
                </section>
            </section>
            <section class="configOrder_xiang">
                <!-- 详情标题 -->
                <header v-if="checkoutData.cart.restaurant_info">
                    <img :src="imageBaseUrl+checkoutData.cart.restaurant_info.image_path" alt="">
                    <span>{{checkoutData.cart.restaurant_info.name}}</span>
                </header>
                <ul class="food_list_ul" v-if="checkoutData.cart.groups">
                    <li v-for="(item,index) in checkoutData.cart.groups[0]" :key="index" class="food_item_style">
                        <p class="ellipsis food_name">{{item.name}}</p>
                        <div class="num_price">
                            <span>x{{item.quantity}}</span>
                            <span>￥{{item.price}}</span>
                        </div>
                    </li>
                </ul>
                <div class="food_item_style">
                    <p class="food_name ellipsis">{{checkoutData.cart.extra[0].name}}</p>
                    <div class="num_price">
                        <span></span>
                        <span>¥ {{checkoutData.cart.extra[0].price}}</span>
                    </div>
                </div>
                <div class="food_item_style">
                    <p class="food_name ellipsis">配送费</p>
                    <div class="num_price">
                        <span></span>
                        <span>¥ {{checkoutData.cart.deliver_amount || 0}}</span>
                    </div>
                </div>
                <div class="food_item_style total_price">
                    <p>订单￥{{checkoutData.cart.total}}</p>
                    <div class="num_price">
                        <span class="ellipsis" style="color:red">待支付</span>
                        <span style="color:#f60">￥{{checkoutData.cart.total}}</span>
                    </div>
                </div>
            </section>
            <section class="confirmOrder_xuqiou">
                <router-link :to='{path:"/confirmOrder/remark",query:{id:checkoutData.cart.id,sig:checkoutData.sig}}' class="header_style">
                    <span>订单备注</span>
                    <div class="rem_type">
                        <span class="ellipsis">{{remarkText || inputText ? remarklist :'口味,偏好等'}}</span>
                         <svg class="address_empty_right">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                        </svg>
                    </div>
                </router-link>
                <router-link :to="checkoutData.invoice.is_available ? '/confirmOrder/invoice' : '' " class="header_style">
                    <span>发票</span>
                    <div class="rem_type">
                        <span class="ellipsis">{{checkoutData.invoice.status_text}}</span>
                        <svg class="address_empty_right">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                        </svg>
                    </div>
                </router-link>
            </section>
            <section class="confirm_orde">
                <p>待支付￥{{checkoutData.cart.total}}</p>
                <p @click="confrimOrder">确定下单</p>
            </section>
            <transition name="fade">
                <div class="cover" v-if="showPayWay" @click="showPayWayFun"></div>
            </transition>
            <transition name="tanchu" v-if="showPayWay">
               <div class="zhifu_tanchu">
                    <header>支付方式</header>
                    <ul>
                        <li v-for="(item,index) in checkoutData.payments" :key="index" :class="{choose:payWayId==item.id}">
                            <span>{{item.name}}<span v-if="!item.is_online_payment">{{item.description}}</span></span>
                             <svg class="address_empty_right" @click="choosePayWay(item.is_online_payment,item.id)">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#select"></use>
                            </svg>
                        </li>
                    </ul>
               </div>   
            </transition>
        </section>
        <loading v-show="showLoading"></loading>
        <transition name="router-slid" mode="out-in">
            <router-view></router-view>
        </transition>
        <alert-tip v-show="showAlert" @closeTip="showAlert = false" :alertText="alertText"></alert-tip>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex'
import headTop from '../../components/header/header'
import alertTip from '../../components/common/alertTip'
import loading from '../../components/common/loading'
import {checkout,getAddress,placeOrders,getAddressList} from '../../service/getData'
import {imageBaseUrl} from '../../config/env'
export default {
    data(){
        return{
            geohash:'',//地址
            shopId:null,//商铺id
            showLoading:true,
            checkoutData:null,//数据返回值
            shopCart:null,//购物车数据
            showPayWay:false,//付款方式
            payWayId:1,//罚款方式id
            showAlert:false,//弹出框
            alertText:'',//弹出框内容
            imageBaseUrl
        }
    },components: {
        headTop,
        alertTip,
        loading,
    },methods: {
        ...mapMutations([
            'INIT_BUYCART','CHANGE_ORDER_PARAM','CHOOSE_ADDRESS','SAVE_GEOHASH','NEED_VALIDATION','SAVE_CART_ID_SIG','SAVE_ORDER_PARAM','ORDER_SUCCESS','SAVE_SHOPID'
        ]),
                //初始化数据
        async initData()
        {
            let newArr=new Array;
            Object.values(this.shopCart).forEach(categoryItem=>{
                Object.values(categoryItem).forEach(itemvuale=>{
                    Object.values(itemvuale).forEach(item=>{
                        newArr.push({
                            attrs:[],
                            extra:[],
                            id:item.id,
                            name:item.name,
                            packing_fee:item.packing_fee,
                            price:item.price,
                            quantity:item.num,
                            sku_id:item.sku_id,
                            specs:[item.specs],
                            stock:item.stock
                        })
                    })
                })
            })
            //检验订单是否满足条件
            this.checkoutData=await checkout(this.geohash,[newArr],this.shopId);
            //保存下单后的id和sig
            this.SAVE_CART_ID_SIG({cart_id:this.checkoutData.cart.id,sig:this.checkoutData.sig});
            this.initAddress();
             this.showLoading = false;
        },
        //地址备注颜色
        iconColor(name)
        {
            switch(name)
            {
                case '公司':return '#4cd964';
                case '学校':return '#3190e8'
            }
        },
        //获取地址信息第一个为默认地址
       async initAddress()
        {
            if(this.userInfo&&this.userInfo.user_id)
            {
                //获取个人中心编辑地址
                const addressRes=await getAddressList(this.userInfo.user_id);
                
                if(addressRes instanceof Array && addressRes.length)
                {
                    //默认选中第一个收货地址存入vuex
                   
                    this.CHOOSE_ADDRESS({addres: addressRes[0], index: 0});
                }
            }
        },
        //显示付款方式
        showPayWayFun()
        {
            this.showPayWay=!this.showPayWay;
        },
        //选择付款方式
        choosePayWay(bool,id)
        {
            //是否支持的支付方式
            if(bool)
            {
                this.payWayId=id;
                this.showPayWay=!this.showPayWay;
            }
        },
        async confrimOrder()
        {
            //alert("s");
            //用户未登陆时弹出提示框
            if(!(this.userInfo&&this.userInfo.user_id))
            {
                this.alertText="用户未登陆";
                this.showAlert=true;
                return;
                //未选择收货地址提示
            }else if(!this.choosedAddress)
            {
                this.showAlert=true;
                this.alertText="请添加一个收货地址";
                return;
            }
            //alert(!this.choosedAddress);
            //保存订单
            //alert(this.remarklist);
            this.SAVE_ORDER_PARAM({
                user_id:this.userInfo.user_id,//用户id
                cart_id:this.checkoutData.cart.id,//订单id
                address_id:this.choosedAddress.id,//地址id
                description:this.remarklist,//备注
                entities:this.checkoutData.cart.groups,//订单分组
                geohash:this.geohash,//地址位置
                sig:this.checkoutData.sig,//订单sig
            })
            //发生订单信息
            let orderRes=await placeOrders(this.userInfo.user_id,this.checkoutData.cart.id,this.choosedAddress.id,this.remarklist,this.checkoutData.cart.groups,this.checkoutData.sig)
            //alert(JSON.stringify(orderRes));
             //第一次下单的手机号需要验证,否之直接下单成功
            if(orderRes.need_validation)
            {
                //保存下单时需要返回值验证
                this.ORDER_SUCCESS(orderRes);
                this.$router.push('/confirmOrder/userValidation');
            }else{
                this.ORDER_SUCCESS(orderRes);
                this.$router.push('/confirmOrder/payment');
            }
        }
    },computed: {
        ...mapState([
            'cartList','remarkText','inputText','invoice','choosedAddress','userInfo'
        ]),
        //备注页返回信息处理
        remarklist:function(){
            console.log("备注");
            let str=new String;
            if(this.remarkText){
                Object.values(this.remarkText).forEach(item=>{
                    str+=item[1]+','
                })
            }
            if(this.inputText)
            {
               return str+this.inputText;
            }else{
                //lastIndexof()寻找最后一个字符的位置
               return str.substr(0,str.lastIndexOf(','));
            }
        }
    },created () {
        this.geohash=this.$route.query.geohash;
        this.shopId=this.$route.query.shopId;
        //商铺id存入vuex;
        this.SAVE_SHOPID(this.shopId);
        //获取购物车数据
        this.INIT_BUYCART();
        this.shopCart=this.cartList[this.shopId];
    },mounted () {
        if(this.geohash)
        {
            this.initData();
        }
    },watch: {
        userInfo:function(values)
        {
            if(values&&userInfo.user_id)
            {
                this.initAddress();
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/mixin.scss';
.confirmOrder_container{
    padding-top:1.95rem;
    padding-bottom: 3rem;
    p, span{
            font-family: Helvetica Neue,Tahoma,Arial;
    }
}
.address_container{
    min-height: 3.5rem;
    @include fj;
    align-items: center;
    padding:0 0.6rem;
    background: url(../../images/address_bottom.png) left bottom repeat-x;//repeat-x背景图像将在水平方向重复。
    background-color: #fff;
    background-size: auto .12rem;
    .address_empty_left{
        display: flex;
        align-items: center;
        .location_icon{
            @include wh(.8rem,.8rem);
            fill:$blue;
        }
        .add_address{
            @include sc(0.7rem,#666);
            width: 10.0rem;
            margin-left:0.3rem;
        }
    }
    .address_empty_right{
        @include wh(.8rem,.8rem);
    }
}
    .router-slid-enter-active, .router-slid-leave-active {
        transition: all .4s;
    }
    .router-slid-enter, .router-slid-leave-active {
        transform: translate3d(2rem, 0, 0);
        opacity: 0;
    }
.address_detail_container{
    header{
        display: flex;
        span:nth-of-type(1)
        {
            @include sc(0.8rem,#333);
            font-weight: 600;
        }
        span:nth-of-type(2){
            @include sc(0.8rem,#333);
            //padding-top: 0.2rem;
            padding-left:0.3rem;
        }
        span:nth-of-type(3)
        {
            @include sc(0.8rem,#333);
            padding-left:0.3rem;
        }
    }
}
.address-detail{
    padding-top: 0.4rem;
    display: flex;
    span{
         @include sc(0.5rem,#fff);
        background-color: #ff5722;
        border-radius: 0.8rem;
        height: 0.7rem;
         width: 1.5rem;
        text-align: center;
    }
    p{
       @include sc(0.5rem,#666); 
       padding-left:0.3rem;
    }
}
.address_detail_container{
    padding:0.4rem;
}
.confirmOrder_songda{
    background: #fff;
    margin-top:0.4rem;
    border-left:0.2rem solid $blue;
    display: flex;
    justify-content: space-around;
    @include wh(100%,4.8rem);
    align-items: center;
    .configOrder_txt{
        @include sc(0.8rem,#666);
        font-weight: 600;
    }
    .configOrder_txs{
        display: flex;
        flex-direction: column;
        align-items: flex-end;//align-items弹性盒子纵轴对齐方式 flex-end靠右边对齐
        p:nth-of-type(1)
        {
            @include sc(0.7rem,$blue);
        }
        p:nth-of-type(2)
        {
            width: 2.4rem;
            border:1px;
            border-radius: 0.1rem;
            background: $blue;
            text-align: center;
            margin-top: 0.3rem;
            @include sc(0.5rem,#fff);
        }
    }
}
.configOrder_zifu{
    background: #fff;
    margin-top: 0.4rem;
    .hongbo{
        padding:0.6rem;
        display: flex;
        justify-content: space-between;
        span:nth-of-type(1)
        {
            @include sc(0.6rem,#ccc);
        }
        span:nth-of-type(2)
        {
            @include sc(0.7rem,#666);
        }
    }
    .header_style{
        border-bottom:0.025rem solid #f1f1f1;
        padding:0.5rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        span{
            @include sc(0.7rem,#666);
        }
        .more_type{
            span{
                @include sc(0.6,#ccc);
            }
            svg{
                @include wh(.5rem,.5rem);
                fill: #999;
            }
        }
    }
}
.zhifu_tanchu{
    min-height: 7rem;
    position:fixed;
    bottom: 0;
    width: 100%;
    z-index:204;
    header{
          background-color: #fafafa;
            @include sc(.7rem, #333);
            text-align: center;
            line-height: 2rem;
    }
    ul{
        background: #fff;
        display: flex;
        flex-direction: column;
        li{
          @include fj;
            padding: 0 .7rem;
            line-height: 2.5rem;
            align-items: center;
            span{
                @include sc(.7rem, #ccc);
            }
            svg{
                @include wh(.8rem, .8rem);
                fill: #eee;
            }
        }
        .choose{
            span{
                color:#333;
            }
            svg{
                fill: #4cd964;
            }
        }
    }
}
.cover{
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0,0,0,.3);
        z-index: 203;
}
.fade-enter-active,.fade-leave-active{
    transition:0.3s;
}
.fade-enter,.fade-leave-active{
    opacity: 0;
}
   .tanchu-enter-active, .tanchu-leave-active {
        transition: all .3s;
    }
    .tanchu-enter, .tanchu-leave-active {
        transform: translate3d(0,10rem,0)
    }
.configOrder_xiang{
    background: #fff;
    margin-top: 0.6rem;
    header{
        display: flex;
        align-items: center;
        img{
            @include wh(1.2rem,1.2rem);
        }
        span{
            @include sc(0.8rem,#333);
            padding-left: 0.6rem;
        }
        padding:0.6rem;
        border-bottom: 0.025rem solid #f1f1f1;
    }
}
.food_list_ul{
    padding-top: 0.3rem;
}
    .food_item_style{
        display: flex;
        padding:0.5rem;
        align-items: center;
        border-bottom:0.025rem solid #f1f1f1;
        p{
            flex:4;
            @include sc(0.6rem,#666);
        }
        .num_price{
            flex:1;
            display: flex;
            justify-content: space-between;
            span:nth-of-type(1){
                @include sc(0.6rem,#f60);
            }
            span:nth-of-type(2)
            {
                @include sc(0.6rem,#666);
            }
        }
    }
.confirmOrder_xuqiou{
    margin-top: 0.6rem;
    .header_style{
        border-bottom:0.025rem solid $bc;;
        background: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding:0.8rem;
        span{
            @include sc(0.6rem,#666);
        }
        .rem_type{
            display: flex;
            align-items: center;
            svg{
                @include wh(.7rem,.7rem);
            }
            span{
                @include sc(.6rem,#ccc);
                margin-right: 0.3rem;
            }
        }
    }
}
.confirm_orde{
    position: fixed;
    bottom: 0;
    display: flex;
    width:100%;
    height: 2rem;
    p{
        @include sc(.75rem,#fff);
        line-height: 2rem;
    }
    p:nth-of-type(1)
    {
        background-color: #3c3c3c;
        flex:5;
        padding-left:.7rem;
    }
    p:nth-of-type(2)
    {
        flex:2;
        background-color: #56d176;
        text-align: center;
    }    

}
</style>



